.carousel-inner h3 {
    color: azure;
    padding: 20px;
    animation: anime-H3 1.3s ease-out;
    background-color: rgba(30,29,29, 0.6);
   }
   .carousel-inner p {
    font-size: 18px;
    background-color: rgba(30,29,29, 0.6);
    padding: 20px;
    animation: anime-P 1.6s ease-out;
   }
   
   @keyframes anime-H3 {
    from {
     opacity: 0;
     transform: translateX(-2000px);
    }
    to {
     opacity: 1;
     transform: translateX(0);
    }
   }
   @keyframes anime-P {
    from {
     opacity: 0;
     transform: translateX(2000px);
    }
    to {
     opacity: 1;
     transform: translateX(0);
    }
   }